import React from 'react'
import fetch from '../../fetch/axios';
import '../../style/chatSetting.less';
import { Button, Input, Slider, Table, Modal, Radio, Switch, message, Popconfirm, Select } from 'antd';
import { PlusCircleOutlined, DeleteOutlined, FormOutlined } from '@ant-design/icons';

class PageFrame extends React.Component {
  state = {
    // 模型
    modelList: [],
    saveModelOpen: false,
    inputError: false,
    radioValue: 0,
    modelSelect: "gpt-3.5-turbo",
    editModelObj: {
      "id": 0,
      "resourceName": "",
      "deploymentId": "",
      "apiKey": "",
      "is_active": false,
      "quota_per_month": 0,
      "serviceType": 0
    },
    // 参数
    argList: [],
    argNeedSave: false,
    temperature: 0,
    top_p: 0,
    max_tokens: 0,
    presence_penalty: 0,
    frequency_penalty: 0,
    history_msg_count: 0,
  }
  componentDidMount() {
    this.getModelList();
    this.getArguments();
  }

  // 获取模型列表
  getModelList() {
    fetch.get(`/api/Setting/openai/list`).then((res) => {
      if (res && res.success) {
        // console.log(res.data)
        this.setState({
          modelList: res.data
        })
      }
    })
  }

  // 打开模型弹窗
  openModel(item) {
    this.setState({
      saveModelOpen: true
    })
    if (item) {
      this.setState({
        editModelObj: item,
        radioValue: item.quota_per_month === 0 ? 0 : 1
      })
    }
  }

  // 重置模型
  resetModel() {
    this.setState({
      saveModelOpen: false,
      editModelObj: {
        "id": 0,
        "resourceName": "",
        "deploymentId": "",
        "apiKey": "",
        "is_active": false,
        "quota_per_month": 0,
        "serviceType": 0,
      },
      radioValue: 0,
      modelSelect: "gpt-3.5-turbo",
      inputError: false,
    })
  }

  // 修改参数
  changeModel(e, type) {
    let { editModelObj } = this.state;
    editModelObj[type] = e?.target?.value.trim() || "";
    if (type === 'quota_per_month') {
      if (parseInt(e.target.value) <= 0) {
        editModelObj.quota_per_month = 0;
        this.setState({radioValue: 0})
      } else {
        this.setState({radioValue: 1})
      }
    }
    this.setState({editModelObj})
  }
  // 修改radio
  changeRadio(e) {
    let { editModelObj } = this.state;
    if (e.target.value === 0) {
      editModelObj.quota_per_month = 0;
      this.setState({editModelObj});
    } else if (e.target.value === 1 && editModelObj.quota_per_month === 0) {
      editModelObj.quota_per_month = 2000;
      this.setState({editModelObj});
    }
    this.setState({
      radioValue: e.target.value
    })
  }
  // 修改switch
  changeSwitch(e) {
    let { editModelObj } = this.state;
    editModelObj.is_active = e;
    this.setState({editModelObj})
  }
  // 修改select
  changeSelect(e) {
    let { editModelObj } = this.state;
    editModelObj.serviceType = e;
    this.setState({editModelObj})
  }
  // 修改模型
  changeModelSelect(e) {
    this.setState({modelSelect: e})
  }

  // 保存模型
  saveModel() {
    let { editModelObj } = this.state;
    if ((!editModelObj.resourceName || !editModelObj.deploymentId || !editModelObj.apiKey) && editModelObj.serviceType === 0) {
      this.setState({ inputError: true });
      return;
    }
    if (!editModelObj.apiKey && editModelObj.serviceType === 1) {
      this.setState({ inputError: true });
      return;
    }
    let obj = {
      "id": editModelObj.id,
      // "resourceName": editModelObj.resourceName,
      // "deploymentId": editModelObj.deploymentId,
      "apiKey": editModelObj.apiKey,
      "is_active": editModelObj.is_active,
      "quota_per_month": parseInt(editModelObj.quota_per_month),
      "serviceType": editModelObj.serviceType,
    }
    if (editModelObj.serviceType === 0) {
      obj.resourceName = editModelObj.resourceName;
      obj.deploymentId = editModelObj.deploymentId;
    } else {
      obj.resourceName = "";
      obj.deploymentId = this.state.modelSelect;
    }
    fetch.post(`/api/Setting/openai/Save`, obj).then((res) => {
      if (res && res.success) {
        message.success('保存成功');
        this.setState({
          saveModelOpen: false
        })
        this.getModelList();
        this.resetModel();
      }
    })
  }

  // 删除模型
  deleteModel(id) {
    fetch.post(`/api/Setting/openai/delete/${id}`).then((res) => {
      if (res && res.success) {
        message.success('删除成功');
        this.getModelList();
      }
    })
  }

  // 获取详情
  getModelDetail(id) {
    fetch.get(`/api/Setting/openai/${id}`).then((res) => {
      if (res && res.success) {
        this.setState({
          editModelObj: res.data
        })
      }
    })
  }


  // 获取参数
  getArguments() {
    fetch.get(`/api/Setting`).then((res) => {
      if (res && res.success) {
        let arr = res.data;
        let temperature, top_p, max_tokens, presence_penalty, frequency_penalty, history_msg_count;

        arr.forEach((item, index) => {
          if (item.key_name === 'Model_temperature') {
            temperature = item.data_value;
          } else if (item.key_name === 'Model_top_p') {
            top_p = item.data_value;
          } else if (item.key_name === 'Model_max_tokens') {
            max_tokens = item.data_value;
          } else if (item.key_name === 'Model_presence_penalty') {
            presence_penalty = item.data_value;
          } else if (item.key_name === 'Model_frequency_penalty') {
            frequency_penalty = item.data_value;
          } else if (item.key_name === 'Model_history_msg_count') {
            history_msg_count = item.data_value;
          }
        })
        this.setState({
          argList: res.data,
          temperature,
          top_p,
          max_tokens,
          presence_penalty,
          frequency_penalty,
          history_msg_count,
        })
      }
    })
  }

  changeArg(e, type, max) {
    if (!max) max = 1;
    let value = parseFloat(e?.target?.value || 0).toFixed(2);
    if ( value > max ) {
      value = max;
    }
    if ( value <= 0 ) {
      value = 0;
    }
    this.setState({
      [type]: value,
      argNeedSave: true,
    })
  }

  changeArgSlide(e, type) {
    this.setState({
      [type]: e || 0,
      argNeedSave: true,
    })
  }

  // 保存参数
  saveArg() {
    // 遍历参数列表，如果有修改的，就保存
    const { argList, temperature, top_p, max_tokens, presence_penalty, frequency_penalty, history_msg_count } = this.state;

    let successCount = 0;
    argList.forEach((item, index) => {
      if (item.key_name === 'Model_temperature' && item.data_value !== temperature) {
        successCount++;
        this.saveArgItem(item.id, temperature, () => { successCount--; });
      } else if (item.key_name === 'Model_top_p' && item.data_value !== top_p) {
        successCount++;
        this.saveArgItem(item.id, top_p, () => { successCount--; });
      } else if (item.key_name === 'Model_max_tokens' && item.data_value !== max_tokens) {
        successCount++;
        this.saveArgItem(item.id, max_tokens, () => { successCount--; });
      } else if (item.key_name === 'Model_presence_penalty' && item.data_value !== presence_penalty) {
        successCount++;
        this.saveArgItem(item.id, presence_penalty, () => { successCount--; });
      } else if (item.key_name === 'Model_frequency_penalty' && item.data_value !== frequency_penalty) {
        successCount++;
        this.saveArgItem(item.id, frequency_penalty, () => { successCount--; });
      } else if (item.key_name === 'Model_history_msg_count' && item.data_value !== history_msg_count) {
        successCount++;
        this.saveArgItem(item.id, history_msg_count, () => { successCount--; });
      }
    })
    let count = 0;
    let timer = setInterval(() => {
      count++;
      if (successCount === 0) {
        clearInterval(timer);
        this.setState({
          argNeedSave: false,
        })
        message.success('Save successfully');
      }
      if (count > 60 ) {
        clearInterval(timer);
        message.error('Save failed');
      }
    }, 1000)
  }
  saveArgItem(id, value, callback) {
    fetch.post(`/api/Setting/Save`, {
      id,
      value: `${value}`
    }).then((res) => {
      if (res && res.success) {
        return callback && callback();
      }
    })
  }

  // 重置参数
  resetArg() {
    fetch.post(`/api/Setting/reset`).then((res) => {
      if (res && res.success) {
        message.success('Reset successfully');
        this.getModelList();
        this.getArgList();
      }
    })
  }

  // 清除聊天
  clearChat() {
    fetch.post(`/api/Setting/cleardata`).then((res) => {
      if (res && res.success) {
        message.success('Clear successfully');
      }
    })
  }

  // 替换中间的字符为*
  replaceMiddleWithAsterisk(str) {
    const length = str.length;
    if (length <= 8) {
      // 如果字符串长度小于等于8，则返回原始字符串
      return str;
    } else {
      const middle = length - 8;
      const asterisks = '*'.repeat(middle);
      return str.substring(0, 4) + asterisks + str.substring(length - 4);
    }
  };

  render() {
    const columns = [
      {
        title: 'Model Name',
        dataIndex: 'deploymentId',
        key: 'deploymentId',
      },
      {
        title: '服务提供商',
        dataIndex: 'serviceType_Text',
        key: 'serviceType_Text',
      },
      {
        title: 'Key',
        dataIndex: 'apiKey',
        key: 'apiKey',
        render: (text, record) => {
          return (
            <div>
              {this.replaceMiddleWithAsterisk(text)}
            </div>
          )
        }
      },
      {
        title: 'Endpoint',
        dataIndex: 'resourceName',
        key: 'resourceName',
      },
      {
        title: '使用量限制（每人月）',
        dataIndex: 'quota_per_month',
        key: 'quota_per_month',
      },
      {
        title: '启用状态',
        dataIndex: 'is_active',
        key: 'is_active',
        render: (text, record) => {
          return (
            <div>
              {text ? '启用' : '禁用'}
            </div>
          )
        }
      },
      {
        title: '操作',
        dataIndex: 'id',
        key: 'id',
        render: (text, record) => {
          return (
            <div style={{display: 'flex'}}>
              <Popconfirm
                placement="topRight"
                title={"警告"}
                description={"您确定要删除该模型吗？"}
                onConfirm={()=>{ this.deleteModel(text) }}
                okText="Yes"
                cancelText="No"
              >
                <Button size='small' type='text' icon={<DeleteOutlined />} danger></Button>
              </Popconfirm>
              <Button size='small' type='text' icon={<FormOutlined />} onClick={() => { this.openModel(record) }} style={{marginLeft: ".25rem"}}></Button>
            </div>
          )
        }
      },
    ];
    const { editModelObj } = this.state;
    return (
      <div className="nc_settings">
        <div className='nc_card'>
          <div className='nc_line'>
            <div className='ncl_lt'>
              <div className='ncl_lt_title' style={{fontSize: "16px", fontWeight: "700"}}>模型（model）</div>
            </div>
            <div className='ncl_it'>
              <Button size='middle' icon={<PlusCircleOutlined />} onClick={() => { this.openModel() }}>新建</Button>
            </div>
          </div>
          <Table columns={columns} dataSource={this.state.modelList} rowKey={record => record.id }/>
        </div>
       
        {/*<div className='nc_card'>*/}
        {/*  <div className='nc_line'>*/}
        {/*    <div className='ncl_lt'>*/}
        {/*      <div className='ncl_lt_title' style={{fontSize: "16px", fontWeight: "700"}}>参数（arguments）</div>*/}
        {/*    </div>*/}
        {/*    {this.state.argNeedSave &&*/}
        {/*      <Button type='primary' onClick={()=>{ this.saveArg() }}>保存</Button>*/}
        {/*    }*/}
        {/*  </div>*/}
        {/*  <div className='nc_line'>*/}
        {/*    <div className='ncl_lt'>*/}
        {/*      <div className='ncl_lt_title'>随机性 (temperature)</div>*/}
        {/*      <div className='ncl_lt_des'>值越大，回复越随机</div>*/}
        {/*    </div>*/}
        {/*    <div className='ncl_it'>*/}
        {/*      <Input size='middle' placeholder='0.5' value={this.state.temperature} onChange={(e)=>{ this.changeArg(e, "temperature", 1) }} type='number' step={0.01}/>*/}
        {/*      <Slider*/}
        {/*        min={0}*/}
        {/*        max={1}*/}
        {/*        onChange={(e)=>{ this.changeArgSlide(e, "temperature") }}*/}
        {/*        value={this.state.temperature}*/}
        {/*        step={0.01}*/}
        {/*        className='nc_slider'*/}
        {/*      />*/}
              
        {/*    </div>*/}
        {/*  </div>*/}
        {/*  <div className='nc_line'>*/}
        {/*    <div className='ncl_lt'>*/}
        {/*      <div className='ncl_lt_title'>单次回复限制 (max_tokens)</div>*/}
        {/*      <div className='ncl_lt_des'>单次交互所用的最大 Token 数</div>*/}
        {/*    </div>*/}
        {/*    <div className='ncl_it'>*/}
        {/*      <Input size='middle' placeholder='64' value={this.state.max_tokens} onChange={(e)=>{ this.changeArg(e, "max_tokens", 5000) }} type='number' step={1}/>*/}
        {/*      <Slider*/}
        {/*        min={0}*/}
        {/*        max={5000}*/}
        {/*        onChange={(e)=>{ this.changeArgSlide(e, "max_tokens") }}*/}
        {/*        value={this.state.max_tokens}*/}
        {/*        step={1}*/}
        {/*        className='nc_slider'*/}
        {/*      />*/}
        {/*    </div>*/}
        {/*  </div>*/}
        {/*  <div className='nc_line'>*/}
        {/*    <div className='ncl_lt'>*/}
        {/*      <div className='ncl_lt_title'>话题新鲜度 (presence_penalty)</div>*/}
        {/*      <div className='ncl_lt_des'>值越大，越有可能扩展到新话题</div>*/}
        {/*    </div>*/}
        {/*    <div className='ncl_it'>*/}
        {/*      <Input size='middle' placeholder='0.5' value={this.state.presence_penalty} onChange={(e)=>{ this.changeArg(e, "presence_penalty", 1) }} type='number' step={0.01}/>*/}
        {/*      <Slider*/}
        {/*        min={0}*/}
        {/*        max={1}*/}
        {/*        onChange={(e)=>{ this.changeArgSlide(e, "presence_penalty") }}*/}
        {/*        value={this.state.presence_penalty}*/}
        {/*        step={0.01}*/}
        {/*        className='nc_slider'*/}
        {/*      />*/}
        {/*    </div>*/}
        {/*  </div>*/}
        {/*  <div className='nc_line'>*/}
        {/*    <div className='ncl_lt'>*/}
        {/*      <div className='ncl_lt_title'>频率惩罚度 (frequency_penalty)</div>*/}
        {/*      <div className='ncl_lt_des'>值越大，越有可能降低重复字词</div>*/}
        {/*    </div>*/}
        {/*    <div className='ncl_it'>*/}
        {/*      <Input size='middle' placeholder='0.5' value={this.state.frequency_penalty} onChange={(e)=>{ this.changeArg(e, "frequency_penalty", 1) }} type='number' step={0.01}/>*/}
        {/*      <Slider*/}
        {/*        min={0}*/}
        {/*        max={1}*/}
        {/*        onChange={(e)=>{ this.changeArgSlide(e, "frequency_penalty") }}*/}
        {/*        value={this.state.frequency_penalty}*/}
        {/*        step={0.01}*/}
        {/*        className='nc_slider'*/}
        {/*      />*/}
        {/*    </div>*/}
        {/*  </div>*/}
        {/*  <div className='nc_line'>*/}
        {/*    <div className='ncl_lt'>*/}
        {/*      <div className='ncl_lt_title'>附带历史消息数</div>*/}
        {/*      <div className='ncl_lt_des'>每次请求携带的历史消息数</div>*/}
        {/*    </div>*/}
        {/*    <div className='ncl_it'>*/}
        {/*      <Input size='middle' placeholder='1' value={this.state.history_msg_count} onChange={(e)=>{ this.changeArg(e, "history_msg_count", 100) }} type='number' step={1}/>*/}
        {/*      <Slider*/}
        {/*        min={0}*/}
        {/*        max={100}*/}
        {/*        onChange={(e)=>{ this.changeArgSlide(e, "history_msg_count") }}*/}
        {/*        value={this.state.history_msg_count}*/}
        {/*        step={1}*/}
        {/*        tooltip={{*/}
        {/*          formatter: (v) => {*/}
        {/*            if (v === 100) {*/}
        {/*              return "无限制"*/}
        {/*            } else {*/}
        {/*              return v*/}
        {/*            }*/}
        {/*          },*/}
        {/*        }}*/}
        {/*        className='nc_slider'*/}
        {/*      />*/}
        {/*    </div>*/}
        {/*  </div>*/}
        {/*</div>*/}
        {/*<div className='nc_card'>*/}
        {/*  <div className='nc_line'>*/}
        {/*    <div className='ncl_lt'>*/}
        {/*      <div className='ncl_lt_title'>重置所有设置</div>*/}
        {/*      <div className='ncl_lt_des'>重置所有设置项回默认值</div>*/}
        {/*    </div>*/}
        {/*    <div className='ncl_it'>*/}
        {/*      <Popconfirm*/}
        {/*        placement="topRight"*/}
        {/*        title={"警告"}*/}
        {/*        description={"您确定要重置所有设置吗？"}*/}
        {/*        onConfirm={()=>{ this.resetArg() }}*/}
        {/*        okText="Yes"*/}
        {/*        cancelText="No"*/}
        {/*      >*/}
        {/*        <Button size='small'>立即重置</Button>*/}
        {/*      </Popconfirm>*/}
        {/*    </div>*/}
        {/*  </div>*/}
        {/*  <div className='nc_line'>*/}
        {/*    <div className='ncl_lt'>*/}
        {/*      <div className='ncl_lt_title'>清除所有数据</div>*/}
        {/*      <div className='ncl_lt_des'>清除所有聊天、设置数据</div>*/}
        {/*    </div>*/}
        {/*    <div className='ncl_it'>*/}
        {/*      <Popconfirm*/}
        {/*        placement="topRight"*/}
        {/*        title={"警告"}*/}
        {/*        description={"您确定要清除所有聊天、设置数据吗？"}*/}
        {/*        onConfirm={()=>{ this.clearChat() }}*/}
        {/*        okText="Yes"*/}
        {/*        cancelText="No"*/}
        {/*      >*/}
        {/*        <Button size='small'>立即清除</Button>*/}
        {/*      </Popconfirm>*/}
        {/*    </div>*/}
        {/*  </div>*/}
        {/*</div>*/}
       
        <Modal title="模型 (model)" open={this.state.saveModelOpen} onOk={()=>{ this.saveModel() }} onCancel={()=>{ this.resetModel() }}>
          <div className='chat_setting_modal'>
            <div className='cs_model_title'>模型提供商</div>
            <Select
              value={editModelObj.serviceType}
              onChange={(e)=>{ this.changeSelect(e) }}
              options={[
                {
                  value: 0,
                  label: 'Azure OpenAI',
                },
                {
                  value: 1,
                  label: 'OpenAI',
                },
              ]}
            />
            {editModelObj.serviceType === 0 && (
              <>
                <div className='cs_model_title'>Model Name</div>
                <Input
                  value={editModelObj.deploymentId}
                  onChange={(e)=>{ this.changeModel(e, "deploymentId") }}
                  status={!editModelObj.deploymentId && this.state.inputError && "error"}
                ></Input>
              </>
            )}
            {editModelObj.serviceType === 1 && (
              <>
                <div className='cs_model_title'>Model</div>
                <Select
                  value={this.state.modelSelect}
                  onChange={(e)=>{ this.changeModelSelect(e) }}
                  options={[
                    {
                      value: 'gpt-3.5-turbo',
                      label: 'gpt-3.5-turbo',
                    },
                    {
                      value: 'gpt-3.5-turbo-0301',
                      label: 'gpt-3.5-turbo-0301',
                    },
                    {
                      value: 'gpt-4',
                      label: 'gpt-4',
                    },
                    {
                      value: 'gpt-4-32k',
                      label: 'gpt-4-32k',
                    },
                  ]}
                />
              </>
            )}
            <div className='cs_model_title'>Key</div>
            <Input
              value={editModelObj.apiKey}
              onChange={(e)=>{ this.changeModel(e, "apiKey") }}
              status={!editModelObj.apiKey && this.state.inputError && "error"}
            ></Input>
            {editModelObj.serviceType === 0 && (
              <>
                <div className='cs_model_title'>Endpoint</div>
                <Input
                  value={editModelObj.resourceName}
                  onChange={(e)=>{ this.changeModel(e, "resourceName") }}
                  status={!editModelObj.resourceName && this.state.inputError && "error"}
                ></Input>
              </>
            )}
            <div className='cs_model_title'>使用量限制（每人月）</div>
            <Radio.Group onChange={(e)=>{ this.changeRadio(e) }} value={this.state.radioValue}>
              <Radio value={0}>无限制</Radio>
              <Radio value={1}>限制</Radio>
              <Input type='number' value={editModelObj.quota_per_month} onChange={(e)=>{ this.changeModel(e, "quota_per_month") }}></Input>
            </Radio.Group>
            <div className='cs_model_title'>启用状态</div>
            <Switch checkedChildren="启用" unCheckedChildren="禁用" checked={editModelObj.is_active} onChange={(e)=>{ this.changeSwitch(e) }}/>
          </div>
        </Modal>
      </div>
    )
  }
}

export default PageFrame